import React, { useState } from "react";
import ReactMarkdown from "react-markdown"; // react-markdown 
import "./App.css"; // CSS  

function AppTest() {
  //      
  const [inputValue, setInputValue] = useState("");
  const [responseMessage, setResponseMessage] = useState(""); //    
  const [isLoading, setIsLoading] = useState(false); //   
  const [strValue, setStrValue] = useState(
    "I understand that this is not ideal, but it is the only feasible way for the payment to be made in full"
  ); // STR   

  //     
  const handleInputChange = (event) => {
    setInputValue(event.target.value); //  
  };

  // STR   
  const handleStrChange = (event) => {
    setStrValue(event.target.value); // STR 
  };

  //    
  const handleSubmit = async (event) => {
    event.preventDefault(); //    
    setIsLoading(true); //     
    setResponseMessage("");
    try {
      const response = await fetch("http://localhost:8080/api", {
        method: "POST", // POST 
        headers: {
          "Content-Type": "application/json", // JSON  
        },
        body: JSON.stringify({
          input: "@@" + strValue + "@@ , $$" + inputValue + "$$", // STR  
        }), //  JSON  
      });

      if (response.ok) {
        const data = await response.json(); // JSON  
        setResponseMessage(data.message); //    
      } else {
        setResponseMessage("  ");
      }
    } catch (error) {
      console.error("   :", error);
      setResponseMessage("   .");
    }
    setIsLoading(false); //    
  };

  const handleKeyDown = (event) => {
    if (isLoading) {
      event.preventDefault(); //     
      return;
    }
    // (13)     
    if (event.key === "Enter") {
      event.preventDefault(); //  
      handleSubmit(event); //   
    }
  };

  return (
    <div className="App">
      <div className="english-sentence-container">
        {/*  STR        */}
        <textarea
          type="text"
          value={strValue} // STR   
          onChange={handleStrChange} // STR     
          placeholder="   "
          className="input-field"
        />
      </div>
      <h1>
                 
        !
      </h1>
      <form onSubmit={handleSubmit} className="form-container">
        <textarea
          type="text"
          value={inputValue} // input   
          onChange={handleInputChange} //     
          onKeyDown={handleKeyDown} //     
          placeholder=" "
          className="input-field"
          disabled={isLoading} //    
        />
        <button type="submit" className="submit-button" disabled={isLoading}>
          {isLoading ? " ..." : ""}
        </button>
      </form>
      <div className="markdown-container">
        {isLoading && <div className="loading-spinner"></div>}
        <div className="markdown-output">
          <ReactMarkdown>{responseMessage}</ReactMarkdown>
        </div>
      </div>
      {/* <p> : {responseMessage}</p>     */}
    </div>
  );
}

export default AppTest;
